import {Component, OnInit} from '@angular/core';
import {Field} from '../dynamic-form-builder/dynamic-field-builder/field.interface';
import {Validators} from "@angular/forms";
import {Subject} from "rxjs/index";
import {SchoolsService} from "../../services/schools/schools.service";

@Component({
    selector: 'app-add-new-student',
    templateUrl: './add-new-student.component.html',
    styleUrls: ['./add-new-student.component.scss']
})
export class AddNewStudentComponent implements OnInit {

    public subjectSchools = new Subject();

    public fields: Field[] = [
        {
            type: 'input',
            name: 'firstName',
            placeholder: ' ',
            value: '',
            validations: [
                {
                    name: 'required',
                    fn: Validators.required,
                    text: ' '
                },
                {
                    name: 'minlength',
                    fn: Validators.minLength(2),
                    text: '   2 '
                }
            ]
        }
        , {
            type: 'input',
            name: 'lastName',
            placeholder: ' ',
            value: '',
            validations: [
                {
                    name: 'minlength',
                    fn: Validators.minLength(2),
                    text: '   2 '
                }
            ]
        },
        {
            type: 'dropdown',
            name: 'schoolName',
            placeholder: '  ',
            value: '',
            options: this.subjectSchools.asObservable(),
            validations: [
                {
                    name: 'required',
                    fn: Validators.required,
                    text: ' '
                },
                {
                    name: 'minlength',
                    fn: Validators.minLength(2),
                    text: '   2 '
                }
            ]
        },
        {
            type: 'input',
            name: '_class',
            placeholder: '',
            value: '',
            validations: [
                {
                    name: 'minlength',
                    fn: Validators.minLength(2),
                    text: '   2 '
                }, {
                    name: 'required',
                    fn: Validators.required,
                    text: ' '
                }
            ]
        },
        {
            type: 'input',
            name: 'email',
            placeholder: '',
            value: '',
            validations: [
                {
                    name: 'email',
                    fn: Validators.email,
                    text: '   .'
                }
            ],
        },
        {
            type: 'input',
            name: 'phoneNumber',
            placeholder: '',
            value: '',
            validations: [
                {
                    name: 'phoneNumber',
                    fn: Validators.email,
                    text: '   .'
                }
            ]
        },
        {
            type: 'input',
            name: 'paid',
            placeholder: ' ',
            value: '',
            validations: [
                {
                    name: 'phoneNumber',
                    fn: Validators.required,
                    text: '   .'
                }
            ]
        },
        {
            type: 'textarea',
            name: 'note',
            placeholder: '',
            value: '',
            validations: [
                {
                    name: 'required',
                    fn: Validators.required,
                    text: '   .'
                }
            ]
        }
    ];

    constructor(private schoolsService: SchoolsService) {
        this.schoolsService.get$().subscribe((schools) => {

            if (!schools) {
                return;
            }

            const options = schools.map((school) => {
                return {
                    text: school.name,
                    value: school._id,
                };
            });

            this.subjectSchools.next(options);
        });
    }

    ngOnInit() {

    }

    onSubmit(event) {

    }

}
